package com.myntra.appscore

import com.myntra.appscore.database.helloSQL.HelloQueries
import com.myntra.appscore.database.newInstance
import com.myntra.appscore.database.schema
import com.squareup.sqldelight.Transacter
import com.squareup.sqldelight.db.SqlDriver

public interface MainDatabase : Transacter {
  public val helloQueries: HelloQueries

  public companion object {
    public val Schema: SqlDriver.Schema
      get() = MainDatabase::class.schema

    public operator fun invoke(driver: SqlDriver): MainDatabase =
        MainDatabase::class.newInstance(driver)
  }
}
