package com.myntra.appscore.database

import com.myntra.appscore.MainDatabase
import com.myntra.appscore.database.helloSQL.HelloQueries
import com.myntra.appscore.database.helloSQL.HockeyPlayer
import com.squareup.sqldelight.Query
import com.squareup.sqldelight.TransacterImpl
import com.squareup.sqldelight.`internal`.copyOnWriteList
import com.squareup.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Int
import kotlin.Long
import kotlin.String
import kotlin.Unit
import kotlin.collections.MutableList
import kotlin.reflect.KClass

internal val KClass<MainDatabase>.schema: SqlDriver.Schema
  get() = MainDatabaseImpl.Schema

internal fun KClass<MainDatabase>.newInstance(driver: SqlDriver): MainDatabase =
    MainDatabaseImpl(driver)

private class MainDatabaseImpl(
  driver: SqlDriver
) : TransacterImpl(driver), MainDatabase {
  public override val helloQueries: HelloQueriesImpl = HelloQueriesImpl(this, driver)

  public object Schema : SqlDriver.Schema {
    public override val version: Int
      get() = 1

    public override fun create(driver: SqlDriver): Unit {
      driver.execute(null, """
          |CREATE TABLE hockeyPlayer (
          |  player_number INTEGER NOT NULL,
          |  full_name TEXT NOT NULL
          |)
          """.trimMargin(), 0)
      driver.execute(null, "CREATE INDEX hockeyPlayer_full_name ON hockeyPlayer(full_name)", 0)
      driver.execute(null, """
          |INSERT INTO hockeyPlayer (player_number, full_name)
          |VALUES (15, 'Ryan Getzlaf')
          """.trimMargin(), 0)
    }

    public override fun migrate(
      driver: SqlDriver,
      oldVersion: Int,
      newVersion: Int
    ): Unit {
    }
  }
}

private class HelloQueriesImpl(
  private val database: MainDatabaseImpl,
  private val driver: SqlDriver
) : TransacterImpl(driver), HelloQueries {
  internal val selectAll: MutableList<Query<*>> = copyOnWriteList()

  public override fun <T : Any> selectAll(mapper: (player_number: Long, full_name: String) -> T):
      Query<T> = Query(-1586218163, selectAll, driver, "Hello.sq", "selectAll", """
  |SELECT *
  |FROM hockeyPlayer
  """.trimMargin()) { cursor ->
    mapper(
      cursor.getLong(0)!!,
      cursor.getString(1)!!
    )
  }

  public override fun selectAll(): Query<HockeyPlayer> = selectAll { player_number, full_name ->
    HockeyPlayer(
      player_number,
      full_name
    )
  }

  public override fun insert(player_number: Long, full_name: String): Unit {
    driver.execute(757172081, """
    |INSERT INTO hockeyPlayer(player_number, full_name)
    |VALUES (?, ?)
    """.trimMargin(), 2) {
      bindLong(1, player_number)
      bindString(2, full_name)
    }
    notifyQueries(757172081, {database.helloQueries.selectAll})
  }

  public override fun insertFullPlayerObject(hockeyPlayer: HockeyPlayer): Unit {
    driver.execute(-1793155264, """
    |INSERT INTO hockeyPlayer(player_number, full_name)
    |VALUES (?, ?)
    """.trimMargin(), 2) {
      bindLong(1, hockeyPlayer.player_number)
      bindString(2, hockeyPlayer.full_name)
    }
    notifyQueries(-1793155264, {database.helloQueries.selectAll})
  }
}
