package com.myntra.appscore.database

import com.squareup.sqldelight.db.SqlDriver
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import com.myntra.appscore.MainDatabase
import kotlin.js.JsExport

class SQLDatabase(
    val driverProvider: suspend ((schema: SqlDriver.Schema) -> SqlDriver)
) {
    private var mainDatabase: MainDatabase? = null

    suspend fun getDatabase(): MainDatabase {
        if (mainDatabase == null) {
            val driver = driverProvider(MainDatabase.Schema)
            mainDatabase =  MainDatabase(driver)
        }
        mainDatabase?.helloQueries?.insert(7, "0")
        return mainDatabase!!
    }

    suspend operator fun <R> invoke(block: suspend MainDatabase.() -> R): R =
        getDatabase().block()

}



val sqlDatabase = SQLDatabase(::createDriver)

@JsExport
fun helloSQL1() {
    println("PRINT SQL DATA")
    GlobalScope.launch {
        println("PRINT SQL DATA")
        sqlDatabase {
            println("PRINT SQL DATA")
            helloQueries.selectAll { player_number, full_name ->
                println("PRINT SQL DATA, $player_number, $full_name")
            }.executeAsList()
        }
    }
}